import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Header from "../components/header/Header";
import Barnav from "../components/nav/Nav";
import Footers from "../components/footer/Footer";
import "./home.css";
import Imagenes from "../assets/Imagenes";

import { Container, Row, Col, Button } from "react-bootstrap";
import WhatsAppButton from "../components/btnWhatsapp/Whatsapp";
import ControlledCarousel from "../components/Carrusel/carrusel";

function Homepage() {
	return (
		<div className="Homepage">
			<Header />
			<Barnav />
			<WhatsAppButton
				mensaje={"Hola estoy interesado en adquirir un collar smartpet"}
			/>
			<div>
				<header className="header">
					<h1 className="title">Bienvenido a SmartPet</h1>
					<p className="subtitle">
						Collares inteligentes para tus mascotas
					</p>
					<Button
						variant="primary"
						onClick={() => {
							window.open(
								"https://wa.me/+5493412275598?text=Hola! Estoy interesado en comprar un collar SmarPet!",
								"_blank"
							);
						}}
						className="cta-button"
					>
						Comprar Ahora
					</Button>
				</header>

				<section className="section section-body">
					<Container>
						<Row>
							<Col md={7} sm={12} lg={8}>
								<h2>Qu es SmartPet?</h2>
								<p>
									SmartPet es un colgante identificatorio para
									mascotas que ofrece dos opciones para
									acceder a la informacin de tu mascota.
									Puedes escanear el cdigo QR con la cmara
									de tu telfono o utilizar el chip
									inteligente basado en NFC. Al acercar tu
									telfono al chip NFC, sers redirigido
									automticamente a una base de datos en lnea
									donde se encuentran los datos de la mascota.
									De esta manera, cualquier persona puede
									acceder a la informacin utilizando el QR o
									el NFC.
								</p>
							</Col>
							<Col md={5} sm={12} lg={4}>
								<div className="container-carrusel">
									<ControlledCarousel
										a={Imagenes.imgs5}
										b={Imagenes.imgs1}
										c={Imagenes.imgs3}
										d={Imagenes.imgs2}
										IMGCarrusel="ImgCarrusel "
									/>
								</div>
								<img
									src={Imagenes.imgs5}
									alt="SmartPet Collars"
									className="img-fluid imgs-body esconder"
								/>
							</Col>
						</Row>
					</Container>
				</section>

				<section className=" container section section-body bg-light">
					<Container>
						<Row>
							<Col
								md={5}
								sm={12}
								lg={4}
								className="d-none d-md-block"
							>
								<img
									src={Imagenes.imgs1}
									alt="Pet with SmartPet Collar"
									className="img-fluid imgs-body"
								/>
							</Col>
							<Col md={7} sm={12} lg={8}>
								<h2>Cmo funciona SmartPet?</h2>
								<p>
									El usuario recibe el colgante con un cdigo
									de 4 letras y 3 nmeros, nico e
									irrepetible. El usuario se registra en la
									pgina de SmartPet y agrega el cdigo para
									poder configurar su mascota. La base de
									datos contiene una imagen de la mascota,
									nombre, sexo, ciudad, edad, descripcin
									(tratamientos importantes), y contactos de 2
									personas con botones de acceso directo a
									Instagram, llamada telefnica y WhatsApp.
								</p>
								<p>Cuando alguien escanea el QR, recibirs una notificacin con su ubicacin* y se mostrarn las vas de contacto que hayas elegido. Esto permite un contacto directo con el dueo sin importar si ha perdido su telfono, adems de ofrecerte el beneficio agregado de recibir la ubicacin de tu mascota en el momento en que alguien escanee su QR.

*Recibirs una notificacin que informa que alguien ha escaneado el QR de tu mascota y la ubicacin aproximada. Si la persona que escanea el QR comparte permisos de ubicacin, tambin obtendrs la ubicacin precisa.</p>
							</Col>
						</Row>
					</Container>
				</section>

				<section className="section section-body ">
					<Container>
						<Row>
							<Col md={7} sm={12} lg={8}>
								<h2>Caractersticas nicas</h2>
								<p>
									La digitalizacin de los datos no sensibles
									y tiles de tu mascota con solo escanear un
									QR o acercar el telfono celular al collar.
									Estn hechos con resina epoxi, altamente
									personalizables, y tambin ofrecemos
									collares sublimados con diseos
									personalizados.
								</p>
							</Col>
							<Col md={5} sm={12} lg={4} className="esconder">
								<img
									src={Imagenes.imgs4}
									alt="Customizable SmartPet Collars"
									className="img-fluid imgs-body esconder"
								/>
							</Col>
						</Row>
					</Container>
				</section>

				<section className="section section-body bg-light">
					<Container>
						<Row>
							<Col md={5} sm={8} lg={4} className="esconder">
								<img
									src={Imagenes.imgs3}
									alt="Pet with SmartPet Collar"
									className="img-fluid imgs-body esconder"
								/>
							</Col>
							<Col md={7} sm={12} lg={8} col={6}>
								<h2>Opciones de Personalizacin</h2>
								<p>
									Puedes escoger el color, tamao, el uso o no
									de chip NFC, y editar el contenido de la
									base de datos de tu mascota. La produccin
									de un collar personalizado lleva de 3 a 5
									das. Los collares sublimados se entregan en
									1 a 2 das.
								</p>
							</Col>
						</Row>
					</Container>
				</section>
			</div>
			<Footers />
		</div>
	);
}

export default Homepage;
